<template>
  <div class="registration">
    <page-title first-title="秒杀活动报名"></page-title>
    <div class="header title-header">
      <div class="in">
        <div class="in-left">报名须知：</div>
        <div class="in-right">
          <div>1.限时秒杀活动无法指定日期、场次及场次的数量投放，介意者请勿报名；</div>
          <div>
            2.请保证提交报名10天内您提报的秒杀价格有效，商品库存>秒杀总库存，原价>秒杀价，原价现金>秒杀现金，每场秒杀库存由运营设置，库存一般为10~20，请您按您的情况，设置足够的秒杀总库存，10天内未被选中，可能您的商品不符合本次活动主题，建议您关注其他报名通知；
          </div>
          <div>
            3.报名成功后，活动未开始到结束期间运营可删除商品，可取消场次秒杀活动，商户不能自行操作，需要删除、取消的，请自行联系运营。
          </div>
        </div>
      </div>
    </div>
    <div class="transfer-info">
      <el-form :inline="true">
        <el-form-item label="商品分类">
          <el-select v-model="params.catId" multiple placeholder="请选择" @remove-tag="removeTag">
            <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value"> </el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" icon="el-icon-search" @click="search">搜索</el-button>
          <el-button type="primary" icon="el-icon-refresh" @click="clear">清空</el-button>
        </el-form-item>
      </el-form>
    </div>

    <div class="transfer-table">
      <el-table
        :data="spikeList"
        stripe
        v-loading="loading"
        style="width: 100%"
        :header-cell-style="{ background: '#eef1f6' }"
      >
        <el-table-column label="序号" type="index" width="80px"></el-table-column>
        <el-table-column label="报名主题" prop="secName" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="支持类目" prop="categoryId"></el-table-column>
        <el-table-column label="报名时间">
          <template v-slot="scope">
            {{ utils.format_date(scope.row.startTime) }}<br />
            至 <br />{{ utils.format_date(scope.row.endTime) }}
          </template>
        </el-table-column>
        <el-table-column label="主题说明" prop="secDesc"></el-table-column>
        <el-table-column label="报名状态" prop="signUpStatusStr"></el-table-column>
        <el-table-column label="操作">
          <template v-slot="scope">
            <el-button type="text" @click="signUp(scope.row)" v-if="scope.row.signUpStatus == 1">去报名</el-button>
            <el-button type="text" @click="signUpInfo(scope.row)" v-if="scope.row.signUpStatus == 2"
              >查看报名详情</el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </div>
    <el-pagination
      background
      @current-change="handleCurrentChange"
      :current-page.sync="currentPage"
      :page-size="params.limit"
      layout="prev, pager, next, jumper"
      :total="total"
    ></el-pagination>
  </div>
</template>

<script>
export default {
  name: 'spikeEventRegistration',
  data() {
    return {
      currentPage: 1,
      total: 0,
      spikeList: [],
      loading: false,
      options: [],
      params: {
        limit: 10,
        page: 1,
        catId: []
      }
    }
  },
  created() {
    this.getProductList()
    this.spikeListArr()
  },
  methods: {
    handleCurrentChange(currentPage) {
      this.params.page = currentPage
      this.spikeListArr()
    },
    search() {
      this.currentPage = 1
      this.params.page = 1
      this.spikeListArr()
    },
    clear() {
      this.currentPage = 1
      this.params.page = 1
      this.params.catId = []
      this.spikeListArr()
    },
    removeTag(e) {
      // console.log(e);
      this.spikeListArr()
    },
    getProductList() {
      this.$api.get('hd/itzSecbuySignup/categoryList', null, res => {
        if (res.status == 200) {
          this.options = res.data
        }
      })
    },
    spikeListArr() {
      this.$api.get(
        `hd/itzSecbuySignup/signup?categoryId=${this.params.catId.join(',')}&type=1&page=${this.params.page}&limit=${
          this.params.limit
        }`,
        null,
        res => {
          if (res.status === 200) {
            this.spikeList = res.data.itzSecbuySignupVoList
            this.total = res.data.total
          }
        }
      )
    },
    /**
     * 报名
     * */
    signUp(n) {
      console.log(n)
      this.$router.push({ path: `registrationDetails/${n.id}`, query: { mark: 'signUp' } })
    },
    /**
     * 查看报名详情
     * */
    signUpInfo(n) {
      console.log(n)
      this.$router.push({ path: `registrationDetails/${n.id}`, query: { mark: 'info' } })
    }
  }
}
</script>

<style scoped lang="scss">
/deep/ .el-form-item {
  margin-top: 22px;
}

.el-pagination {
  text-align: center;
  margin-top: 10px;
}

.registration {
  .title-header {
    margin-top: 10px;
    .in {
      display: flex;
      color: #fb3a40;
      letter-spacing: 1px;
      .in-left {
        width: 74px;
      }
      .in-right {
        flex: 1;
      }
    }
  }
}
</style>
